import React from 'react';
import { rivais } from '../containers/rivais';
import Card from './Card';

const CardList = ({spells, rivais}) => {
    if(true){
        throw new Error('No!')
    }

    return( 
        <div>
            {            
                spells.map((spell, i) => {
                    return( 
                    <Card 
                        key={i} 
                        index={spells[i].index} 
                        nameSpell={spells[i].name} 
                        school={spells[i].school} 
                        nameRivais={rivais[i]}
                    />)
                })
        
        }</div>
    )
}

export default CardList;