import React, { Component } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import ErrorBoundry from '../components/ErrorBoundry';
import {rivais} from './rivais.js'
import './App.css'

class App extends Component {
    constructor(){
    super()
    this.state = {
        spells: [],
        spellsD: [],
        searchfield: ''
    }}

componentDidMount(){
    fetch('https://www.dnd5eapi.co/api/spells')
    .then(response => response.json())
    .then(spells => {this.setState({spells: spells.results})})
    .then(() => {for (var i = 0; i< this.state.spells.length; i++){ 
        fetch(`https://www.dnd5eapi.co${this.state.spells[i].url}`)
            .then(response => response.json())
            .then(spellsD => {
                this.setState({
                    spellsD: [...this.state.spellsD, 
                        {index: spellsD.index, 
                        name: spellsD.name, 
                        school: spellsD.school.name}]
                })
            })
    }})
}

shuffle = (array) => {
    var currentIndex = array.length,  randomIndex;

    while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        [array[currentIndex], array[randomIndex]] = [
        array[randomIndex], array[currentIndex]];
    }
    return array;
}

onSearchChange = (event) => {
    this.setState({searchfield: event.target.value});
}

render(){
    const {spellsD, searchfield} = this.state;
    const filteredSpells = spellsD.filter(spell => {
      return spell.name.toLowerCase().includes(searchfield.toLowerCase())
    })
    return !spellsD.length ?
    <h1>Loading</h1>:
    (
      <div className="tc">
        <h1>ARENA DE GRUMMSH</h1>
        <SearchBox searchChange= {this.onSearchChange}></SearchBox>
        <Scroll>
          <ErrorBoundry>
            <CardList spells= {filteredSpells} rivais={rivais}></CardList>
          </ErrorBoundry>
        </Scroll>
      </div>
    );
  }
}

export default App;